<template>
  <div class="foundMed">
    <hr>
    <a class="textBig" v-bind:href="medUrl">{{ medName }}</a><br><br>
    <p class="textMedium">{{ medRelease }}</p><br><br>
    <p class="textMedium">{{ medComposition }}</p>
    <time class="textBig">{{ medCountry }}</time>
  </div>
</template>

<script>
export default {
  name: 'FoundMed',
  props: ['medName', 'medUrl', 'medComposition', 'medRelease', 'medCountry']
}
</script>

<style scoped>
  p, a {
    float: left;
    margin: 5px;
  }
  time {
    float: right;
    margin: 5px;
  }
</style>



