<template>
  <details id="newPrescContainer" class="container detailsContainer boxshadow boxborder">
    <summary class="detailsSummary textBig">База ліків</summary>
    <div class="detailsContent">
      <input name="medSearchTerm" type="text" title="Пошук препаратів"
             v-bind:placeholder="search_by_name ? 'Пошук за назвою' : 'Пошук за вмістом'" v-on:change="find_med('a')"
             class="textInput textMedium boxborder boxshadow" style="width: calc(100% - 86px); margin-bottom: 10px">
      <label class="switch">
        <input type="checkbox" @click="swap_placeholder()">
        <span class="sliderSwitch boxshadow"></span>
      </label>

      <hr>

      <FoundMed v-for="med in found_meds" :key="med.name" :med-name="med.name" :med-composition="med.composition"
                :med-country="med.county" :med-release="med.release_type" :med-url="med.url" />

    </div>
  </details>
</template>

<script>
import FoundMed from './sub/FoundMed'
export default {
  name: 'MedsDB',
  components: {FoundMed},
  methods: {
    swap_placeholder: function () {
      this.search_by_name = !this.search_by_name
    },
    find_med: function (searchMed) {
      console.log('asd')
      this.$http.post('/search_name_test', {'med': searchMed}).then((response) => {
        console.log(response.data)
      }, response => {
        console.log('request error')
      })
    }
  },
  data: function () {
    return {
      search_by_name: true,
      found_meds: [
        {'name': 'Петроцилін',
          'url': 'facebook.com',
          'composition': '2,3,4,5,6 Петрична сіль 100мл',
          'county': 'Ukraine',
          'release_type': 'Апмули'
        },
        {'name': 'Петродиклак',
          'url': 'twitter.com',
          'composition': 'Петропетричний розчин 146%',
          'county': 'Ukraine',
          'release_type': 'Краплі'
        },
        {'name': 'Петро Гель',
          'url': 'google.com',
          'composition': 'Петроксид Петра',
          'county': 'Ukraine',
          'release_type': 'Гель'
        }
      ]
    }
  }
}
</script>

<style scoped>
  details {
    background: rgba(255, 255, 255, 0.7);
  }
  p {
    margin-top: 0;
    margin-left: 16px;
  }

  ::-webkit-scrollbar {
    display: none;
  }

</style>



